import { Weapon, WeaponHandedness, AttackType } from "../model/weapon";
import BaseModel from "../model";

export class DisplayWeapon extends Weapon  {
    isSelected: boolean = false;
    get altModeTagClassName() {
        return this.altMode ? 'is-warning' : '';
    }
    get altModeTagColorClass() {
        return this.altMode ? 'has-text-warning' : '';
    }

    get attackTypeTagClassName() {
        switch (this.attackType) {
            case AttackType.Strike:
                return 'is-success';
            case AttackType.Thrust:
                return 'is-primary';                
        }
    }

    get attackTypeLabel() {
        switch (this.attackType) {
            case AttackType.Strike:
                return 'Strike';
            case AttackType.Thrust:
                return 'Thrust';                
        }
    }  
    
    get attackTypeTagColorClass() {
        switch (this.attackType) {
            case AttackType.Strike:
                return 'has-text-success';
            case AttackType.Thrust:
                return 'has-text-primary';                
        }
    }
    
    get handednessTagClassName() {
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            return 'is-black';
        } else if (this.handedness & WeaponHandedness.One) {
            return 'is-grey';
        } else if (this.handedness & WeaponHandedness.Two) {
            return 'is-dark';
        }
    }

    get handednessTagColorClass() {
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            return 'has-text-black';
        } else if (this.handedness & WeaponHandedness.One) {
            return 'has-text-light';
        } else if (this.handedness & WeaponHandedness.Two) {
            return 'has-text-grey';
        }
    }

    get handednessLabel() {
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            return '1/2-handed';
        } else if (this.handedness & WeaponHandedness.One) {
            return '1-handed';
        } else if (this.handedness & WeaponHandedness.Two) {
            return '2-handed';
        }
    }
    
    constructor(weapon: Weapon) {
        super();
        Object.assign(this, weapon);
    }
}

