export default abstract class BaseModel {

    constructor() {

    }
    
    abstract getSortKey(): string;

    public static sorter<T extends BaseModel>(a: T, b: T) {
        const _a = a.getSortKey();
        const _b = b.getSortKey();

        if (_a < _b) return -1;
        if (_a > _b) return 1;
        return 0;
    }
}
