import BaseModel from ".";

export class Weapon extends BaseModel {
    id: string;
    name: string;
    attackType: AttackType;
    altMode: boolean;
    handedness: WeaponHandedness;
    stopOnHit: boolean;
    canCombo: boolean;
    canFlinch: boolean;
    pointCost: number;
    loadoutPointCost: number;
    damageNoArmor: WeaponDamage;
    damageLightArmor: WeaponDamage;
    damageMediumArmor: WeaponDamage;
    damageHeavyArmor: WeaponDamage;
    computedDamage: {
        averageDamage: number;
        dps: number;
        cDps: number;
        averageDps: number;
    };

    staminaCost: {
        miss: number; 
        feint: number;
        morph: number;
        drain: number;
        parryDrainNegation: number;
    };

    speed: {
        windup: number;
        combo: number;
        release: number;
        recovery: number;
    };

    computedSpeed: {
        attack: number;
        combo: number;
    };

    length: number;

    getSortKey() {
        return this.name + this.attackType + (this.altMode ? 'B' : 'A');
    }
    
    constructor(data?: string[]) {
        super();
        if (data == null) {
            return;
        }

        this.name =       data[0];
        this.attackType = this.resolveAttackType(data[1]);
        this.altMode =    data[2].toLowerCase() == 'on';
        this.handedness = this.resolveWeaponHandedness(data[3]);                         
        this.stopOnHit =  data[4].toLowerCase() == 'yes';          
        this.canCombo =   data[5].toLowerCase() == 'yes';          
        this.canFlinch =  data[6].toLowerCase() == 'yes';          
        this.pointCost =  parseInt(data[7]);    
        this.loadoutPointCost = parseInt(data[8]);
        this.damageNoArmor = {
            head: parseInt(data[9]),
            torso: parseInt(data[10]),
            legs: parseInt(data[11]),
        };
        this.damageLightArmor = {
            head: parseInt(data[12]),
            torso: parseInt(data[13]),
            legs: parseInt(data[14]),
        };
        this.damageMediumArmor = {
            head: parseInt(data[15]),
            torso: parseInt(data[16]),
            legs: parseInt(data[17]),
        };
        this.damageHeavyArmor = {
            head: parseInt(data[18]),
            torso: parseInt(data[19]),
            legs: parseInt(data[20]),
        };
        this.computedDamage = {
            averageDamage: parseInt(data[21]),
            dps: parseInt(data[22]),
            cDps: parseInt(data[23]),
            averageDps: parseInt(data[24]),
        };
        this.staminaCost = {
            miss: parseInt(data[25]),
            feint: parseInt(data[26]),
            morph: parseInt(data[27]),
            drain: parseInt(data[28]),
            parryDrainNegation: parseInt(data[29]),                       
        };
        this.speed = {
            windup: parseInt(data[30]),
            combo: parseInt(data[31]),
            release: parseInt(data[32]),
            recovery: parseInt(data[33]),
        };
        this.computedSpeed = {
            attack: parseFloat(data[34]),
            combo: parseFloat(data[36]),
        };
        this.length = parseInt(data[36]);
        
        this.generateId();
    }

    private resolveAttackType(value: string) {
        switch (value) {
            case 'Strike':
                return AttackType.Strike;
            case 'Thrust':
                return AttackType.Thrust;            
        }
    }

    private resolveWeaponHandedness(value: string) {
        switch (value) {
            case 'One Handed':
                return WeaponHandedness.One;

            case 'Two Handed':
                return WeaponHandedness.Two;

            case 'One/Two Handed':
                return WeaponHandedness.One | WeaponHandedness.Two;
        }
    }

    private generateId() {
        let handednessStr = '';
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            handednessStr = '3';
        } else if(this.handedness & WeaponHandedness.One) {
            handednessStr = '1';
        } else if (this.handedness & WeaponHandedness.Two) {
            handednessStr = '2';
        }

        this.id = `${this.name.replace(/ /g, '-')}-${this.attackType}-${this.altMode ? '1' : '0'}${handednessStr}`;
    }
}

export enum WeaponHandedness {
    One = 1 << 0,
    Two = 1 << 1,
}

export enum AttackType {
    Strike = 1 << 0,
    Thrust = 1 << 1,
}

export class WeaponDamage {
    head: number;
    torso: number;
    legs: number;
}
