import { Weapon, WeaponHandedness } from "../model/weapon";

const SPRADSHEET_ID = '1HXS_aF9CKeGKg6OecwCw3WobZWcTjDkp_4ipN1vnyVk';

export default class DataProvider {
    cachingDurationSeconds: number;

    private readonly googleApiKey: string;
    public spreadsheet: Spreadsheet;
    
    constructor(googleApiKey: string, cachingDurationSeconds = 60 * 60) {
        this.googleApiKey = googleApiKey;
        this.cachingDurationSeconds = cachingDurationSeconds;
    }

    public async init() {
        this.spreadsheet = await this.getSpreadsheet();
    }

    private buildUrl(method: SheetsApiMethod, params: string = '') {
        let apiMethodPath = '';
        switch (method) {
            case SheetsApiMethod.Get:
                apiMethodPath = '';
                break;
            case SheetsApiMethod.Values:
                apiMethodPath = '/values/';
                break;
            default:
                throw new Error(`Unsupported API method ${method}`);                        
        }

        const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPRADSHEET_ID}${apiMethodPath}${params}?key=${this.googleApiKey}`;
        return url;
    }

    private async requestCached<T extends SheetsApiRespnse>(key: string, url: string) {
        const cached: {expiresAt: string, data: any} = JSON.parse(localStorage.getItem(key) || 'null');

        if (cached && new Date(cached.expiresAt).getTime() >= new Date().getTime()) {
            return <T>cached.data;
        }
        
        const request = await fetch(url);
        const data  = <T>JSON.parse(await request.text());
        
        if (data.error == null) {
            const expiresAt = new Date;
            expiresAt.setSeconds(expiresAt.getSeconds() + this.cachingDurationSeconds);

            localStorage.setItem(key, JSON.stringify({
                data: data,
                expiresAt: expiresAt
            }));
        }

        return data;
    }

    async getSpreadsheet<T extends Spreadsheet>() {
        return await this.requestCached('meta-data', this.buildUrl(SheetsApiMethod.Get)) as T;
    }

    async getSheetData<T extends SheetValues>(name: string) {
        return await this.requestCached(`sheet-data-${name}`, this.buildUrl(SheetsApiMethod.Values, name)) as T;
    }

    async getWeapons(): Promise<Weapon[]> {
        const data = await this.getSheetData<{values: (string[])[]}>(this.spreadsheet.sheets[0].properties.title);
        const result: Weapon[] = [];

        const headerRowCount = 2;
        let rowIndex = -1;

        for (let row of data.values) {
            rowIndex++;
            if (rowIndex < headerRowCount) {
                continue;
            }
            result.push(new Weapon(row));
        }

        result.sort(Weapon.sorter);

        return result;
    }
}

export interface SheetsApiRespnse {
    error?: {
        code: string;
        message: string;
    }
}

export interface SheetValues extends SheetsApiRespnse {
    values?: any[];
}

export interface Spreadsheet extends SheetsApiRespnse {
    spreadsheetId: string;
    properties: {
        title: string;
        locale: string;
        autoRecalc: string;
        timeZone: string;
        defaultFormat: any;
    }

    sheets: {
        properties: {
            sheetId: string;
            title: string;
            index: number;
            sheetType: string;
        }
    }[];

    spreadsheetUrl: string;
}

enum SheetsApiMethod {
    Get = 'get',
    Values = 'values'
}
